const nodelist = document.querySelectorAll('.bookIcon')
const nodelistToArray = Array.apply(null, nodelist);

nodelistToArray.forEach(element => {
    const yellowIcon = "data:image/svg+xml;utf8;base64,";

    const whiteIcon = "data:image/svg+xml;utf8;base64,";

    element.src = whiteIcon;

    element.addEventListener("mouseover", ()=>{
        element.src = yellowIcon;
    });

    element.addEventListener("mouseout", ()=>{
        element.src = whiteIcon;
    });
});

